import Point from './Point';

class Path {
    constructor(data) {
        this.points = [];
        this.init(data);
        console.log(this.points);
    }

    init(data) {
        this.createPoints(data);
        this.reorder();
    }

    createPoints(data) {
        for (let i = 0; i < data.n; i++) {
            this.points.push(
                new Point(this, data)
            )
        }
    }

    reorder() {
        this.points = this.points.sort((a, b) => (a.angle > b.angle) ? 1 : -1);
    }

    get center() {
        let tx, ty, centre;
        tx = 0;
        ty = 0;
        for (let point of this.points) {
            tx += point.x;
            ty += point.y;
        }
        return {
            x: tx / this.points.length,
            y: ty / this.points.length
        };
    }

    get d() {
        let path, i, firstPoint, lastPoint;
        path = '';
        i = 0;
        firstPoint = this.points[0];
        lastPoint = this.points[this.points.length - 1];
        for (let point of this.points) {
            if (i === 0) {
                path += 'M ' + point.x + ' ' + point.y + ' ';
            } else {
                path += 'C ' + point.prev.p2.x + ' ' + point.prev.p2.y + ',';
                path +=  point.p1.x + ' ' + point.p1.y + ',';
                path +=  point.x + ' ' + point.y + ' ';
            }
            i++;
        }
        path += 'C ' + lastPoint.p2.x + ' ' + lastPoint.p2.y + ',';
        path +=  firstPoint.p1.x + ' ' + firstPoint.p1.y + ',';
        path +=  firstPoint.x + ' ' + firstPoint.y + ' ';
        return path;
    }
}

export default Path;