class Point {
    constructor(path, data) {
        this.path = path;
        this.data = data;
        this.x =  Math.random() * data.width * 0.8 + 0.1 * data.width;
        this.y = Math.random() * data.height * 0.8 + 0.1 * data.height;

    }

    get vector() {
        return {
            x: this.path.center.x - this.x,
            y: this.path.center.x - this.y
        }
    }

    get angle() {
        return Math.atan2(this.vector.y, this.vector.x);
    }

    get next() {
        if (this.index < (this.path.points.length - 1)) {
            return this.path.points[this.index + 1];
        } else {
            return this.path.points[0];
        }
    }

    get prev() {
        if (this.index > 0) {
            return this.path.points[this.index - 1];
        } else {
            return this.path.points[this.path.points.length - 1];
        }
    }

    get index() {
        return this.path.points.indexOf(this);
    }

    get sideVector() {
        return {
            x: this.prev.x - this.next.x,
            y: this.prev.y - this.next.y
        }
    }

    get p1() {
        return {
            x: this.x + (this.data.rounding * this.sideVector.x),
            y: this.y + (this.data.rounding * this.sideVector.y)
        }
    }

    get p2() {
        return {
            x: this.x - (this.data.rounding * this.sideVector.x),
            y: this.y - (this.data.rounding * this.sideVector.y)
        }
    }
}

export default Point;