<script>
    import Path from "@/classes/Path";

    export default {
        name: 'blob',
        components: {},
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                path: null
            }
        },
        computed: {
            center() {
                return {
                    x: (0.5 * this.data.width) - this.path.center.x,
                    y: (0.5 * this.data.height) - this.path.center.y
                }
            }
        },
        methods: {
            update() {
                this.updatePoints();
            },
            updatePoints() {
                this.path = new Path(this.data);
            }
        },
        mounted(){
            this.update();
        },
        watch: {
            data: {
                deep: true,
                handler() {
                    this.update();
                }
            }
        }
    }
</script>


<template>
    <div class="blob">
        <svg
            v-if="path"
            :width="data.width"
            :height="data.height">
            <g :transform="'translate(' + center.x + ',' + center.y + ')'">
                <path
                        :d="path.d"
                        fill="rgba(255, 180, 188, 1)"
                        stroke="none"
                        stroke-width="0"/>
                <g
                        v-for="(point, index) in path.points"
                        :transform="'translate(' + point.x + ',' + point.y + ')'">
                    <text>
                        {{index}}
                    </text>
                    <circle
                            r="2"
                            class="circle__base"/>
                    <circle
                            r="2"
                            :cx="point.p1.x - point.x"
                            :cy="point.p1.y - point.y"/>
                    <circle
                            r="2"
                            :cx="point.p2.x - point.x"
                            :cy="point.p2.y - point.y"/>
                </g>
            </g>
        </svg>
    </div>
</template>


<style lang="scss">
    @import '@/styles/variables.scss';

    .blob {

        svg {
            background: #ddd;

            circle {
                fill: red;

                &.circle__base {
                    fill: #000;
                }
            }
        }
    }
</style>